<script lang='ts' setup>
import { reactive, ref } from 'vue'

// api
import { GoogleAPiPhotos } from '@/api/endpoints/google'

//  components
import Item from './components/item/index.vue'
import { IPhoto } from '@/api/endpoints/google/types'

const items: Array<IPhoto> = reactive([])

// dialog
const dialogState = ref(false)
let dialogSelectePhoto = ref()

GoogleAPiPhotos().then((data) => {
  Object.keys(data.photos)
    .map((key: IPhoto) => items[key] = data.photos[key])
}, (err) => {
  console.log('err: ', err)
})

const onChosePhoto = (url) => {
  dialogState.value = true;
  dialogSelectePhoto = require(`@/assets/gallery/${url}`)
  dialogState.value = true;
}
</script>

<template>
  <v-card color="grey-lighten-3">
    <v-container max-width="1200px">
      <div class="row">
        <v-col>
          <div class="gallery-wrapper">
            <div
              class="gallery-photo-wrapper d-inline-flex"
              v-for="item in items"
              :key="item.id"
            >
              <Item
                :id="item.id"
                :url="item.url"
                @chose-photo="onChosePhoto($event)"
              />
            </div>
          </div>
        </v-col>


      </div>
    </v-container>

  </v-card>
  <!-- PHOTOS -->

  <!-- DIALOG -->
  <v-dialog
    v-model="dialogState"
    max-width="800"
  >
    <v-card>
      <template v-slot:text>
        <!-- dialog controls -->
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            text="Close"
            icon="mdi-window-close"
            variant="text"
            @click="dialogState = false"
          ></v-btn>
        </v-card-actions>
        <!-- dialog content -->
        <div class="dialog-img-wrapper">
          <img
            :src="dialogSelectePhoto"
            alt="concretePhotoUrl"
          >
        </div>
      </template>
    </v-card>
  </v-dialog>

</template>
<style scoped>
/* gallery */
.gallery-wrapper {
  text-align: center;
}

.gallery-photo-wrapper {
  padding: 5px;
}

/* dialog */
.dialog-img-wrapper {
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-content: stretch;
}

.dialog-controls {
  text-align: right;
}

.dialog-img-wrapper img {
  /* height: 600px; */
  width: auto;
}
</style>
